import actions from "./actions";

const initState = {
	ApicallData:{},
};

export default function reducer(state = initState, action) {
    switch (action.type) {
     
		case actions.GET_APICALL_DATA:
            return {
                ...state,
                loading: true
            };
        case actions.GET_APICALL_DATA_SUCCESS_RESULT:
        // console.log(action);
            return {
                ...state,
                loading: false,
                ApicallData: action.result
            };
        case actions.GET_APICALL_DATA_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                ApicallData:action
            };


        default:
			return state;

    }
}