import actions from "./actions";

const initState = {
	paramlogs:{},
	dGraphlogs:{},
};

export default function reducer(state = initState, action) {
    switch (action.type) {
     
		case actions.GET_PARAM_LOGS:
            return {
                ...state,
                loading: true
            };
        case actions.GET_PARAM_LOGS_SUCCESS_RESULT:
            return {
                ...state,
                loading: false,
                paramlogs: action.result
            };
        case actions.GET_PARAM_LOGS_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                paramlogs:action
            };

        // dGraph logs
        case actions.GET_DGRAPH_LOGS:
            return {
                ...state,
                loading: true
            };
        case actions.GET_DGRAPH_LOGS_SUCCESS_RESULT:
            return {
                ...state,
                loading: false,
                dGraphlogs: action.result
            };
        case actions.GET_DGRAPH_LOGS_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                dGraphlogs:action
            };


        default:
			return state;

    }
}