// 	await dataAccess.get(url, payload);

// const postRequest = async (url, payload) =>
//     await dataAccess.post(url, payload);
    
const getWithoutHeaderRequest = async (url, payload) =>
    await dataAccess.getWithoutHeader(url, payload);
    
function* getAPICallsData(data) {
    debugger
    const  payload  = data;
    let url ;
    // url = "https://www.getpostman.com/collections/23ea55bef9d55539f902";
    url = NetworkConstants.API_DOCS;
    try {
        let response = yield call(getWithoutHeaderRequest, url,payload)
        console.log('Get API calls Response', response);
        // yield put(actions.getAPICallsDataSuccess("200", response));

        if (200 === response.code) {
            yield put(actions.getAPICallsDataSuccess(response.code, response.res));
        } else {
            debugger;
            yield put(actions.getAPICallsDataError(response.code, response.errMsg));
        }
    } catch (error) {
        // alert('Failed to fetch Blocks API');
        yield put(actions.getAPICallsDataError(1));
    }
}

export default function* rootSaga() {
    yield all(
        [
            takeEvery(actions.GET_APICALL_DATA, getAPICallsData),
        ]);
}