import React  from 'react';
import { withRouter } from 'react-router';
import Media from "react-media";


import { Layout, Menu, Icon  } from 'antd';
import './index.scss';
// 
import { pathConstants } from 'constants/path-constants';


const { Sider } = Layout;

class SideMenu extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            collapsed: true,
            activeMenu:[],
        };
        this.navToPage = this.navToPage.bind(this);
        this.navToHome = this.navToHome.bind(this);
    }

    componentDidMount() {
       console.log('side menu location'+ this.props.history.location.pathname);
       this.setState({activeMenu:[this.props.history.location.pathname]});
   
    }
    
    componentWillReceiveProps(nextProps){
     
        this.setState({activeMenu:[nextProps.history.location.pathname]});

    }

    navToPage  = (event) => {
        // this.setState({activeMenu:event.key});
        this.props.history.push(event.key);
    }

       
    onCollapse = (collapsed) => {
        console.log(collapsed);
        this.setState({ collapsed });
    }

    navToHome(){
        this.props.history.push(pathConstants.BLOCKEXPLORER);
    }

    render(){
       
        return (
            <Media query={{ maxWidth: 768 }}>
            {matches =>
              matches ? (
                //   mobileview
                <Sider className="side-menu"
                        breakpoint="lg"
                        collapsedWidth="0"
                >
                    <div className="side-menu__logo" onClick={this.navToHome} >
                         <h1 style={{ fontSize: '2rem', color:'#fff'}} >param</h1>
                    </div>
                        <Menu theme="dark"
                            mode="inline"
                            defaultSelectedKeys={[pathConstants.BLOCKEXPLORER]}
                            selectedKeys= {this.state.activeMenu}
                            onClick={this.navToPage}
                            >
                            <Menu.Item  key={pathConstants.BLOCKEXPLORER}>
                                    <Icon type="block" />
                                    <span className="nav-text">Block explorer</span>
                            </Menu.Item>
                            <Menu.Item key={pathConstants.MODELEXPLORER}>
                                <Icon type="cluster" />
                                <span className="nav-text">Model explorer</span>
                            </Menu.Item>
                            <Menu.Item key={pathConstants.QUERYCONSOLE}>
                                <Icon type="code" />
                                <span className="nav-text">Query console</span>
                            </Menu.Item>
                            <Menu.Item key={pathConstants.CHAINCODE}>
                                <Icon type="solution" />
                                <span className="nav-text">Chain code</span>
                            </Menu.Item>
                            <Menu.Item key={pathConstants.APICALL}>
                                <Icon type="api" />
                                <span className="nav-text">API Docs</span>
                            </Menu.Item>
                            <Menu.Item key={pathConstants.LOGS}>
                                <Icon type="file-text" />
                                {/* <Icon type="desktop" /> */}
                                <span className="nav-text">Logs</span>
                            </Menu.Item>
                            <Menu.Item key={pathConstants.HEALTHCHECK}>
                                <Icon type="desktop" />
                                <span className="nav-text">Health Check</span>
                            </Menu.Item>
                        </Menu>
                </Sider>
                
              ) : (
                <Sider className="side-menu"
                       collapsible
                       collapsed={this.state.collapsed}
                       onCollapse={this.onCollapse}
                     
                >
                <div className="side-menu__logo" onClick={this.navToHome}>
                { 
                    this.state.collapsed?
                    <h1 style={{ fontSize: '1.2rem', color:'#fff',textAlign:'center' }} >param</h1>
                    :
                    <h1 style={{ fontSize: '2rem', color:'#fff',textAlign:'center' }} >param</h1>

                }
                </div>
                <Menu theme="dark"
                      mode="inline"
                      defaultSelectedKeys={[pathConstants.BLOCKEXPLORER]}
                    //   selectedKeys = {this.props.history.location.pathname}
                      selectedKeys= {this.state.activeMenu}
                      onClick={this.navToPage}>
                    <Menu.Item id={pathConstants.BLOCKEXPLORER} key={pathConstants.BLOCKEXPLORER}>
                            <Icon type="block" />
                            <span className="nav-text">Block explorer</span>
                    </Menu.Item>
                    <Menu.Item id={pathConstants.MODELEXPLORER} key={pathConstants.MODELEXPLORER}>
                        <Icon type="cluster" />
                        <span className="nav-text">Model explorer</span>
                    </Menu.Item>
                    <Menu.Item id={pathConstants.QUERYCONSOLE} key={pathConstants.QUERYCONSOLE}>
                        <Icon type="code" />
                        <span className="nav-text">Query console</span>
                    </Menu.Item>
                    <Menu.Item id={pathConstants.CHAINCODE} key={pathConstants.CHAINCODE}>
                        <Icon type="solution" />
                        <span className="nav-text">Chain code</span>
                    </Menu.Item>
                    <Menu.Item id={pathConstants.APICALL} key={pathConstants.APICALL}>
                        <Icon type="api" />
                        <span className="nav-text">API Docs</span>
                    </Menu.Item>
                    <Menu.Item id={pathConstants.LOGS} key={pathConstants.LOGS}>
                        <Icon type="file-text" />
                        {/* <Icon type="desktop" /> */}
                        <span className="nav-text">Logs</span>
                    </Menu.Item>
                    <Menu.Item id={pathConstants.HEALTHCHECK} key={pathConstants.HEALTHCHECK}>
                        <Icon type="desktop" />
                        <span className="nav-text">Health Check</span>
                    </Menu.Item>
                </Menu>
        </Sider>
              )
            }
          </Media>
           
         
        );
    }
}
export default withRouter(SideMenu);

