const actions = {
    // User Actions
    GET_USER_ACTIONS: "GET_USER_ACTIONS",
    GET_USER_ACTIONS_SUCCESS_RESULT: "GET_USER_ACTIONS_SUCCESS_RESULT",
    GET_USER_ACTIONS_ERROR_RESULT: "GET_USER_ACTIONS_ERROR_RESULT",

    getUserAction: (data,endpointUrl) => ({
        type: actions.GET_USER_ACTIONS,
        payload: { data },
        endpoint: endpointUrl
    }),
    getUserActionSuccess: (saveStatusCode, result) => ({
        type: actions.GET_USER_ACTIONS_SUCCESS_RESULT,
        saveStatusCode,
        result

    }),
    getUserActionError: (saveStatusCode, message) => ({
        type: actions.GET_USER_ACTIONS_ERROR_RESULT,
        saveStatusCode,
        message
    }),

    // Receipt Details
    GET_RECEIPT_DATA: "GET_RECEIPT_DATA",
    GET_RECEIPT_DATA_SUCCESS_RESULT: "GET_RECEIPT_DATA_SUCCESS_RESULT",
    GET_RECEIPT_DATA_ERROR_RESULT: "GET_RECEIPT_DATA_ERROR_RESULT",

    getReceiptData: (data) => ({
        type: actions.GET_RECEIPT_DATA,
        payload: { data },
    }),
    getReceiptDataSuccess: (saveStatusCode, result) => ({
        type: actions.GET_RECEIPT_DATA_SUCCESS_RESULT,
        saveStatusCode,
        result

    }),
    getReceiptDataError: (saveStatusCode, message) => ({
        type: actions.GET_RECEIPT_DATA_ERROR_RESULT,
        saveStatusCode,
        message
    }),

}

export default actions;
