const actions = {

    GET_PARAM_LOGS: "GET_PARAM_LOGS",
    GET_PARAM_LOGS_SUCCESS_RESULT: "GET_PARAM_LOGS_SUCCESS_RESULT",
    GET_PARAM_LOGS_ERROR_RESULT: "GET_PARAM_LOGS_ERROR_RESULT",

    getParamLogsData: (data) => ({
        type: actions.GET_PARAM_LOGS,
        payload: { data }
    }),
    getParamLogsDataSuccess: (saveStatusCode, result) => ({
        type: actions.GET_PARAM_LOGS_SUCCESS_RESULT,
        saveStatusCode,
        result

    }),
    getParamLogsDataError: (saveStatusCode, message) => ({
        type: actions.GET_PARAM_LOGS_ERROR_RESULT,
        saveStatusCode,
        message
    }),

    // dGraph logs
    GET_DGRAPH_LOGS: "GET_DGRAPH_LOGS",
    GET_DGRAPH_LOGS_SUCCESS_RESULT: "GET_DGRAPH_LOGS_SUCCESS_RESULT",
    GET_DGRAPH_LOGS_ERROR_RESULT: "GET_DGRAPH_LOGS_ERROR_RESULT",

    getGraphLogsData: (data) => ({
        type: actions.GET_DGRAPH_LOGS,
        payload: { data }
    }),
    getGraphLogsDataSuccess: (saveStatusCode, result) => ({
        type: actions.GET_DGRAPH_LOGS_SUCCESS_RESULT,
        saveStatusCode,
        result
    }),
    getGraphLogsDataError: (saveStatusCode, message) => ({
        type: actions.GET_DGRAPH_LOGS_ERROR_RESULT,
        saveStatusCode,
        message
    }),
}
export default actions;