import actions from "./actions";

const initState = {
	QueryData:{},
};

export default function reducer(state = initState, action) {
    switch (action.type) {
     
		case actions.GET_QUERY_DATA:
            return {
                ...state,
                loading: true
            };
        case actions.GET_QUERY_DATA_SUCCESS_RESULT:
            return {
                ...state,
                loading: false,
                QueryData: action.result
            };
        case actions.GET_QUERY_DATA_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                QueryData:action
            };


        default:
			return state;

    }
}