import { all, takeEvery, put, call } from 'redux-saga/effects';
import  actions from './actions';
import * as dataAccess from 'utils/ajax';
// import * as ReduxConstants from 'constants/redux-constants'
import * as NetworkConstants from 'constants/api-constants';


// const APP_CONSTANTS = require('../../constants/app-constants');

// const getRequest = async (url, payload) =>
// 	await dataAccess.get(url, payload);

const postRequest = async (url, payload) =>
	await dataAccess.post(url, payload);

function* getQueryData(data) {
    debugger
    const  payload  = data.payload.data;
    let url ;
    url = NetworkConstants.RUN_QUERY;
    try {
        // console.log("Get blocks URL: ", url, " \n\n: payload", payload);
        let response = yield call(postRequest, url, payload)
        console.log('Get query Response', response);
        if (200 === response.status) {
            yield put(actions.getQueryDataSuccess(response.status, response.content));
        } else {
            debugger;
            yield put(actions.getQueryDataError(response.status, response.message));
        }
    } catch (error) {
        // alert('Failed to fetch Blocks API');
        yield put(actions.getQueryDataError(1));
    }
}

export default function* rootSaga() {
    yield all(
        [
            takeEvery(actions.GET_QUERY_DATA, getQueryData),
        ]);
}