import { all, takeEvery, put, call } from 'redux-saga/effects';
import  actions from './actions';
import * as dataAccess from 'utils/ajax';
// import * as ReduxConstants from 'constants/redux-constants'
import * as NetworkConstants from 'constants/api-constants';


// const APP_CONSTANTS = require('../../constants/app-constants');
const getRequest = async (url, payload) =>
	await dataAccess.get(url, payload);

// const postRequest = async (url, payload) =>
//     await dataAccess.post(url, payload);
  
function* getGraphHealthCheck(data) {
    debugger
    const  { payload }  = data;
    let url ;
    url = NetworkConstants.DGRAPH_HEALTHCHECK;
    try {
        // console.log("Get blocks URL: ", url, " \n\n: payload", payload);
        let response = yield call(getRequest, url, payload)
        console.log('Get dgraph healthcheck Response', response);
        if (200 === response.status) {
            yield put(actions.getGraphHealthCheckSuccess(response.status, response.content));
        } else {
            debugger;
            yield put(actions.getGraphHealthCheckError(response.status, response.message));
        }
    } catch (error) {
        // alert('Failed to fetch Blocks API');
        yield put(actions.getGraphHealthCheckError(1));
    }
}

function* getComposerHealthCheck(data) {
    debugger
    const  { payload }  = data;
    let url ;
    url = NetworkConstants.COMPOSER_HEALTHCHECK;
    try {
        // console.log("Get blocks URL: ", url, " \n\n: payload", payload);
        let response = yield call(getRequest, url, payload)
        console.log('composer healthcheck Response', response);
        if (200 === response.code) {
            yield put(actions.getComposerHealthCheckSuccess(response.code, response.res));
        } else {
            debugger;
            yield put(actions.getComposerHealthCheckError(response.code, response.errMsg));
        }
    } catch (error) {
        // alert('Failed to fetch Blocks API');
        yield put(actions.getComposerHealthCheckError(1));
    }
}

function* getParamHealthCheck(data) {
    debugger
    const  { payload }  = data;
    let url ;
    url = NetworkConstants.PARAM_HEALTHCHECK_PING;
    try {
        // console.log("Get blocks URL: ", url, " \n\n: payload", payload);
        let response = yield call(getRequest, url, payload)
        console.log('param healthcheck Response', response);
        if (200 === response.code) {
            yield put(actions.getParamHealthCheckSuccess(response.code, response));
        } else {
            debugger;
            yield put(actions.getParamHealthCheckError(response.code, response));
        }
    } catch (error) {
        // alert('Failed to fetch Blocks API');
        yield put(actions.getParamHealthCheckError(1));
    }
}


export default function* rootSaga() {
    yield all(
        [
            takeEvery(actions.GET_PARAM_CHECK_DATA, getParamHealthCheck),
            takeEvery(actions.GET_DGRAPH_CHECK_DATA, getGraphHealthCheck),
            takeEvery(actions.GET_COMPOSER_CHECK_DATA, getComposerHealthCheck),
        ]);
}