import { all, takeEvery, put, call } from 'redux-saga/effects';
import actions from './actions';
import * as dataAccess from 'utils/ajax';
// import * as ReduxConstants from 'constants/redux-constants'
import * as NetworkConstants from 'constants/api-constants';


const getRequest = async (url, payload) =>
    await dataAccess.get(url, payload);

const postRequest = async (url, payload) =>
    await dataAccess.post(url, payload);

function* getUserAction(action) {
    debugger;
    try {
        let url =NetworkConstants.BASE_URL+ action.endpoint;
        console.log("getUserAction URL: ", url, " \n\n: payload",  action.payload);
        let response = yield call(postRequest, url, action.payload.data)
        console.log("getUserAction payload", JSON.stringify(action.payload.data));        
        debugger
        if (200 === response.code) {
            yield put(actions.getUserActionSuccess(response.code, response));
        } else {
            yield put(actions.getUserActionError(response.code, response.err));
        }
    } catch (err) {
        console.log("saga error " + err)
        yield put(actions.getUserActionError(1));
    }
}

function* getReceiptData(action) {
    console.log("receiptDetailData action: ",JSON.stringify(action));
    debugger
    try {
        let url = NetworkConstants.RECEIPT_DETAILS+action.payload.data;
        console.log("receiptDetailData URL: ", url);
        debugger
        let response = yield call(getRequest, url, undefined)
        debugger
        if (200 === response.code) {
            yield put(actions.getReceiptDataSuccess(response.code, response.res));
        } else {
            yield put(actions.getReceiptDataError(response.code, response.res.info));
        }
    } catch (err) {
        console.log("saga error "+err)
        yield put(actions.getReceiptDataError(1));
    }
}

export default function* rootSaga() {
    debugger;
	yield all(
		[
            takeEvery(actions.GET_USER_ACTIONS, getUserAction),
            takeEvery(actions.GET_RECEIPT_DATA, getReceiptData),
		]);
}
