const actions = {

    // dGraph healthcheck
    GET_DGRAPH_CHECK_DATA: "GET_DGRAPH_CHECK_DATA",
    GET_DGRAPH_CHECK_DATA_SUCCESS_RESULT: "GET_DGRAPH_CHECK_DATA_SUCCESS_RESULT",
    GET_DGRAPH_CHECK_DATA_ERROR_RESULT: "GET_DGRAPH_CHECK_DATA_ERROR_RESULT",

    getGraphHealthCheck: (data) => ({
        type: actions.GET_DGRAPH_CHECK_DATA,
        payload: { data }
    }),
    getGraphHealthCheckSuccess: (saveStatusCode, result) => ({
        type: actions.GET_DGRAPH_CHECK_DATA_SUCCESS_RESULT,
        saveStatusCode,
        result

    }),
    getGraphHealthCheckError: (saveStatusCode, message) => ({
        type: actions.GET_DGRAPH_CHECK_DATA_ERROR_RESULT,
        saveStatusCode,
        message
    }),

    //composer healthcheck
    GET_COMPOSER_CHECK_DATA: "GET_COMPOSER_CHECK_DATA",
    GET_COMPOSER_CHECK_DATA_SUCCESS_RESULT: "GET_COMPOSER_CHECK_DATA_SUCCESS_RESULT",
    GET_COMPOSER_CHECK_DATA_ERROR_RESULT: "GET_COMPOSER_CHECK_DATA_ERROR_RESULT",

    getComposerHealthCheck: (data) => ({
        type: actions.GET_COMPOSER_CHECK_DATA,
        payload: { data }
    }),
    getComposerHealthCheckSuccess: (saveStatusCode, result) => ({
        type: actions.GET_COMPOSER_CHECK_DATA_SUCCESS_RESULT,
        saveStatusCode,
        result

    }),
    getComposerHealthCheckError: (saveStatusCode, message) => ({
        type: actions.GET_COMPOSER_CHECK_DATA_ERROR_RESULT,
        saveStatusCode,
        message
    }), 

    //param healthcheck
    GET_PARAM_CHECK_DATA: "GET_PARAM_CHECK_DATA",
    GET_PARAM_CHECK_DATA_SUCCESS_RESULT: "GET_PARAM_CHECK_DATA_SUCCESS_RESULT",
    GET_PARAM_CHECK_DATA_ERROR_RESULT: "GET_PARAM_CHECK_DATA_ERROR_RESULT",

    getParamHealthCheck: (data) => ({
        type: actions.GET_PARAM_CHECK_DATA,
        payload: { data }
    }),
    getParamHealthCheckSuccess: (saveStatusCode, result) => ({
        type: actions.GET_PARAM_CHECK_DATA_SUCCESS_RESULT,
        saveStatusCode,
        result

    }),
    getParamHealthCheckError: (saveStatusCode, message) => ({
        type: actions.GET_PARAM_CHECK_DATA_ERROR_RESULT,
        saveStatusCode,
        message
    }), 

}
export default actions;