import React  from 'react';
import { withRouter } from 'react-router';
import { Layout, Menu, Icon , Dropdown} from 'antd';

import { pathConstants } from 'constants/path-constants';
import './index.scss';

const { Header } = Layout;

class SideMenuHeader extends React.Component{
    constructor(props){
        super(props);
        this.navigateToProflie=this.navigateToProflie.bind(this);
    }

    removeSlash = (path) => {
        var title;
        let value = path.replace('/', '');
        value = value.split('/');
        if(value[0] === "block-explorer"){
            title = "Network Dashboard";
            return title;
        }else if(value[0] === "transactiondetail"){
            title = "transaction details";
            return title;
        }else if(value[0] === "query"){
            title = "graph query";
            return title;
        }else if(value[0] === "api-docs"){
            title = "api docs";
            return title;
        }
        else if(value[0] === "model-explorer"){
            title = "model explorer";
            return title;
        }else{
            return value[0];
        }
    }

    navigateToProflie(){
        console.log("PROFILE CLICKED")
    }

    render() {
        const menu = (
            <Menu>
              <Menu.Item key="0">
                <a onClick={this.navigateToProflie}>Profile</a>
              </Menu.Item>
              <Menu.Item key="1">
                <a >Network Setting</a>
              </Menu.Item>
            </Menu>
        );
       
        return (

              <Header className="header">
                    <a>{
                        this.props.location.pathname === '/' ?
                            this.removeSlash(pathConstants.DASHBOARD)
                        : 
                            this.removeSlash(this.props.location.pathname)
                    }
                    </a>
                   
                    <Dropdown className="u_float_right" overlay={menu} trigger={['click']}>
                        <a className="ant-dropdown-link">
                            <Icon type="setting" />
                        </a>
                    </Dropdown>
              </Header>
        );
    }
}
export default withRouter(SideMenuHeader);
