const actions = {

    GET_APICALL_DATA: "GET_APICALL_DATA",
    GET_APICALL_DATA_SUCCESS_RESULT: "GET_APICALL_DATA_SUCCESS_RESULT",
    GET_APICALL_DATA_ERROR_RESULT: "GET_APICALL_DATA_ERROR_RESULT",

    getAPICallsData: (data) => ({
        type: actions.GET_APICALL_DATA,
        payload: { data }
    }),
    getAPICallsDataSuccess: (saveStatusCode, result) => ({
        type: actions.GET_APICALL_DATA_SUCCESS_RESULT,
        saveStatusCode,
        result

    }),
    getAPICallsDataError: (saveStatusCode, message) => ({
        type: actions.GET_APICALL_DATA_ERROR_RESULT,
        saveStatusCode,
        message
    }),
}
export default actions;