import { combineReducers } from 'redux';
import blockExplorer from './blockExplorer/reducers';
import modelExplorer from './modelExplorer/reducers'
import receipts from './receipts/reducers';
import query from './query/reducers';
import apicalls from './apicalls/reducers';
import logs from './logs/reducers';
import healthcheck from './healthcheck/reducers';

export default combineReducers({
    blockExplorer,
    modelExplorer,
    receipts,
    query,
    apicalls,
    logs,
    healthcheck,
});