import { all, takeEvery, put, call } from 'redux-saga/effects';
import actions from './actions';
import * as dataAccess from 'utils/ajax';
// import * as ReduxConstants from 'constants/redux-constants'
import * as NetworkConstants from 'constants/api-constants';


const getRequest = async (url, payload) =>
    await dataAccess.get(url, payload);

// const postRequest = async (url, payload) =>
//     await dataAccess.post(url, payload);

function* getGraphNode(data) {
    debugger
    let url = "";
    const { payload } = data;
    if (payload.data === undefined)
        url = NetworkConstants.GRAPH_NODE;
    else
        url = NetworkConstants.GRAPH_NODE+"/"+payload.data
    try {
        console.log("Graph Node URL: ", url, " \n\n: payload", payload.data);
        let response = yield call(getRequest, url, payload)
        console.log('getGraphNode Response', response);
        response = dataAccess.parseGraphPayload(response);
        debugger
        if (0 === response.statusCode) {
            yield put(actions.getGraphNodeSuccess(response.statusCode, response.response));
        } else {
            yield put(actions.getGraphNodeError(response.code, response.res.info));
        }
    } catch (error) {
        yield put(actions.getGraphNodeError(1));
    }
}

function* getGraphLink(data) {
    debugger
    const { payload } = data;
    let url = NetworkConstants.GRAPH_LINK + payload.data;
    try {
        console.log("Graph Link URL: ", url, " \n\n: payload", payload);
        let response = yield call(getRequest, url, payload)
        console.log('getGraphLink Response', response);
        response = dataAccess.parseGraphPayload(response);
        debugger
        if (0 === response.statusCode) {
            yield put(actions.getGraphLinkSuccess(response.statusCode, response.response));
        } else {
            yield put(actions.getGraphLinkError(response.statusCode, response.errMessage));
        }
    } catch (error) {
        debugger
        yield put(actions.getGraphLinkError(1));
    }
}

export default function* rootSaga() {
  
	yield all(
		[
            takeEvery(actions.GET_GRAPH_NODE, getGraphNode),
            takeEvery(actions.GET_GRAPH_LINKS, getGraphLink)
        ]);
}
