import actions from "./actions";

const initState = {
	dGraphCheckData:{},
	composerCheckData:{},
	paramCheckData:{},
};

export default function reducer(state = initState, action) {
    switch (action.type) {
     
		case actions.GET_DGRAPH_CHECK_DATA:
            return {
                ...state,
                loading: true
            };
        case actions.GET_DGRAPH_CHECK_DATA_SUCCESS_RESULT:
        // console.log(action);
            return {
                ...state,
                loading: false,
                dGraphCheckData: action.result
            };
        case actions.GET_DGRAPH_CHECK_DATA_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                dGraphCheckData:action
            };


        // composer healthcheck
        case actions.GET_COMPOSER_CHECK_DATA:
            return {
                ...state,
                loading: true
            };
        case actions.GET_COMPOSER_CHECK_DATA_SUCCESS_RESULT:
            return {
                ...state,
                loading: false,
                composerCheckData: action.result
            };
        case actions.GET_COMPOSER_CHECK_DATA_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                composerCheckData:action
            };

         // param healthcheck
         case actions.GET_PARAM_CHECK_DATA:
         return {
             ...state,
             loading: true
         };
        case actions.GET_PARAM_CHECK_DATA_SUCCESS_RESULT:
            return {
                ...state,
                loading: false,
                paramCheckData: action.result
            };
        case actions.GET_PARAM_CHECK_DATA_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                paramCheckData:action
            };



        default:
			return state;

    }
}