import { all, takeEvery, put, call } from 'redux-saga/effects';
import  actions from './actions';
import * as dataAccess from 'utils/ajax';
// import * as ReduxConstants from 'constants/redux-constants'
import * as NetworkConstants from 'constants/api-constants';


// const APP_CONSTANTS = require('../../constants/app-constants');
const getRequest = async (url, payload) =>
	await dataAccess.get(url, payload);

// const postRequest = async (url, payload) =>
// 	await dataAccess.post(url, payload);

function* getDashboardStatus(data) {

	const { payload } = data;
	let url = NetworkConstants.BLOCK_DASHBOARD_URL;
	// let url = process.env.REACT_APP_API_BASE_URL + NetworkConstants.BLOCK_DASHBOARD_URL;

	try {
		// console.log("Dashboard URL: ", url, " \n\n: payload", payload);
		let response = yield call(getRequest, url, payload)
		console.log('Get Dashboard Status Response', response);
		// response = dataAccess.parsePayload(response);
		if (200 === response.code) {
			yield put(actions.getDashboardStatusSuccess(response.code, response.res));
		} else {
			yield put(actions.getDashboardStatusError(response.code, response.res.info));
		}
	} catch (error) {
		// alert('Failed to fetch Dashboard status API');
		yield put(actions.getDashboardStatusError(1));
	}
}

