function* getBlockTransaction(data) {
	debugger;
	const { payload } = data;
	let url = NetworkConstants.TRANSACTIONS_URL+'?trxid='+payload.data.txn;
	try {
		// console.log("block txn URL: ", url, " \n\n: payload", payload);
		let response = yield call(getRequest, url, payload)
		console.log('Get block txn  Response', response);
		if (200 === response.code) {
			yield put(actions.getBlockTransactionSuccess(response.code, response.res));
		} else {
			yield put(actions.getBlockTransactionError(response.code, response.errMsg));
		}
	} catch (error) {
		// alert('Failed to fetch data');
		yield put(actions.actions.getBlockTransactionError(1));
	}
}

function* getBlocks(data) {
	debugger
	const { payload } = data;
	let url ;
	if(payload.data === undefined)
	url = NetworkConstants.BLOCKS_URL;
	else
	url=payload.data
	try {
		// console.log("Get blocks URL: ", url, " \n\n: payload", payload);
		let response = yield call(getRequest, url, payload)
		console.log('Get blocks Response', response);
		if (200 === response.code) {
			yield put(actions.getBlocksSuccess(response.code, response.res));
		} else {
			debugger;
			yield put(actions.getBlocksError(response.code, response.errMsg));
		}
	} catch (error) {
		// alert('Failed to fetch Blocks API');
		yield put(actions.getBlocksError(1));
	}
}

// Get transactions
function* getTransactions(data) {
	const { payload } = data;
	if(data.endpoint === undefined){
		data.endpoint = 1;
	}
	// const endpoint = '?startIndex='+data.endpoint+'&pageSize=10';
	let url = NetworkConstants.TRANSACTIONS_URL;
	try {
		// console.log("Get transactions URL: ", url, " \n\n: payload", payload);
		let response = yield call(getRequest, url, payload)
		console.log('Get transactions Response', response);
		// response = dataAccess.parsePayload(response);
		if (200 === response.code) {
			yield put(actions.getTransactionSuccess(response.code, response.res));
		} else {
			debugger;
			yield put(actions.getTransactionError(response.code, response.errMsg));
		}
	} catch (error) {
		debugger;
		// alert('Failed to fetch Transaction API');
		yield put(actions.getTransactionError(1));
	}
}


