// Get transactions Graph
function* getTransactionsGraph(data) {
	debugger
	const { payload } = data;
	let url = NetworkConstants.TRANSACTION_GRAPH;
	try {
		// console.log("Get transactions URL: ", url, " \n\n: payload", payload);
		let response = yield call(getRequest, url, payload)
		console.log('Get transactions Response', response);
		// response = dataAccess.parsePayload(response);
		if (200 === response.code) {
			debugger;
			yield put(actions.getTransactionGraphSuccess(response.code, response.res));
		} else {
			debugger;
			yield put(actions.getTransactionGraphError(response.code, response.errMsg));
		}
	} catch (error) {
		debugger;
		// alert('Failed to fetch chart API');
		yield put(actions.getTransactionGraphError(1));
	}
}




export default function* rootSaga() {
	yield all(
		[
			takeEvery(actions.GET_DASHBOARD_STATUS, getDashboardStatus),
			takeEvery(actions.GET_BLOCKS, getBlocks),
			// takeEvery(actions.GET_CHAIN_TRANSACTION, getChainTransaction),
			takeEvery(actions.GET_TRANSACTION, getTransactions),
			takeEvery(actions.GET_TRANSACTION_GRAPH, getTransactionsGraph),
			takeEvery(actions.GET_BLOCK_TRANSACTION, getBlockTransaction),

			
		]);
}