import actions from "./actions";

const initState = {
    userTransactionData: [],
    receiptDetailData: []
};
export default function reducer(state = initState, action) {
    switch (action.type) {

        // Get User Actions
        case actions.GET_USER_ACTIONS:
            return {
                ...state,
                loading: true
            };
        case actions.GET_USER_ACTIONS_SUCCESS_RESULT:
            // console.log(action.result)
            return {
                ...state,
                loading: false,
                userTransactionData: action.result
            };
        case actions.GET_USER_ACTIONS_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                userTransactionData: []
            };

        // Get Receipt Details
        case actions.GET_RECEIPT_DATA:
            return {
                ...state,
                loading: true
            };
        case actions.GET_RECEIPT_DATA_SUCCESS_RESULT:
            // console.log(action.result)
            return {
                ...state,
                loading: false,
                receiptDetailData: action.result
            };
        case actions.GET_RECEIPT_DATA_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                receiptDetailData: []
            };

        default:
            return state;
    }
}