    GET_TRANSACTIONS_DATA_ERROR_RESULT: "GET_TRANSACTIONS_DATA_ERROR_RESULT",


    getTransactionData: (data) => ({
        type: actions.GET_TRANSACTIONS_DATA,
        payload: { data }
    }),
    getTransactionDataSuccess: (saveStatusCode, result) => ({
        type: actions.GET_TRANSACTIONS_DATA_SUCCESS_RESULT,
        saveStatusCode,
        result
    }),
    getTransactionDataError: (saveStatusCode, message) => ({
        type: actions.GET_TRANSACTIONS_DATA_ERROR_RESULT,
        saveStatusCode,
        message
    }),


