    GET_BLOCKS: "GET_BLOCKS",
    GET_BLOCKS_SUCCESS_RESULT: "GET_BLOCKS_SUCCESS_RESULT",
    GET_BLOCKS_ERROR_RESULT: "GET_BLOCKS_ERROR_RESULT",

    getBlocks: (data) => ({
        type: actions.GET_BLOCKS,
        payload: { data }
    }),
