import React, { Component } from 'react';
import { withRouter  } from 'react-router';
import { Switch, Route } from 'react-router-dom';
import Loadable from 'react-loadable';
import { Layout,Icon, Spin } from 'antd';

// import Loader from 'components/loader';
import NavHeader from '../containers/header';
import NavSider from '../containers/side-menu';
import { pathConstants } from '../constants/path-constants';
import NotFoundRedirect from 'pages/not-found-page';

import './index.scss';

const { Content} = Layout;

const antIcon = <Icon type="loading" style={{ fontSize: 24 }} spin />;

function Loading() {
	return <div className="sk-rotating-plane">
		<Spin indicator={antIcon} />
	</div>;
}

// const Login = Loadable({
//     loader: () => import('../containers/login'),
//     loading: Loader,
// });

const WelcomeAbroad = Loadable({
	loader: () => import('../pages/block-explorer-page'),
	loading: Loading
});

const BlockExplorer = Loadable({
	loader: () => import('../pages/block-explorer-page'),
	loading: Loading
});

const ModelExplorer = Loadable({
	loader: () => import('../pages/model-explorer'),
	loading: Loading
});

const Chaincode = Loadable({
	loader: () => import('../pages/chaincode-page'),
	loading: Loading
});

const APIcalls = Loadable({
	loader: () => import('../pages/api-calls-page'),
	loading: Loading
});

const Logs = Loadable({
	loader: () => import('../pages/logs-page'),
	loading: Loading
});
const HealthCheck = Loadable({
	loader: () => import('../pages/health-check-page'),
	loading: Loading
});

const Receipts = Loadable({
	loader: () => import('../pages/receipts'),
	loading: Loading
});

const QueryConsole = Loadable({
	loader: () => import('../pages/query-console-page'),
	loading: Loading
});

const Static = Loadable({
	loader: () => import('../pages/build-static-page'),
	loading: Loading
});



class Router extends Component {
	

	componentDidMount() {
		if(this.props.location.pathname === '/'){
			// console.log(this.props.location.pathname);
			this.props.history.push(pathConstants.BLOCKEXPLORER);
		}
	}
	

    render() {
		return (
			<Layout className="layout"> 
				<NavSider />

				<Layout>
					<NavHeader />
					<Content className="ui-container">
					
							{/* <div > */}
								<Switch>
									
									<Route exact path='/' component={WelcomeAbroad} />
									<Route path= {pathConstants.BLOCKEXPLORER} component={BlockExplorer} />
									
									<Route path= {pathConstants.MODELEXPLORER} component={ModelExplorer} />
									<Route path= {pathConstants.CHAINCODE} component={Chaincode} />
									<Route path= {pathConstants.APICALL} component={APIcalls} />
									<Route path= {pathConstants.LOGS} component={Logs} />
									<Route path= {pathConstants.HEALTHCHECK} component={HealthCheck} />
									
									
									<Route path= {pathConstants.BLOCKLIST} component={BlockExplorer} />
									<Route path= { pathConstants.BLOCKDETAIL } component={ BlockExplorer }/>
									<Route path= { pathConstants.RECEIPTDETAILS } component={BlockExplorer} />
									<Route path= { pathConstants.TRANSACTIONS } component={BlockExplorer} />
									<Route path= { pathConstants.TRANSACTION_DETAILS } component={BlockExplorer} />
									<Route path= { pathConstants.PROFILE } component={BlockExplorer} />
									<Route path= { pathConstants.RECEIPTS } component={Receipts} />
									<Route path= { pathConstants.QUERYCONSOLE } component={QueryConsole} />
									
									<Route path= { pathConstants.STATIC } component={Static} />
									<Route  component = {NotFoundRedirect} />
								</Switch >
							{/* </div> */}
						
					</Content>
				</Layout>
			</Layout>
		);
	}
}
export default withRouter(Router);

