import actions from "./actions";

const initState = {
	DashboardStatus: {},
	Blocktransaction: {},
	TransactionTableData: {},
	Blocks: {},
	Transactions: {},
	TransactionGraph:{},
	chainTransactions:{},
};
export default function reducer(state = initState, action) {
	switch (action.type) {

		// Get Dashboard status
		case actions.GET_DASHBOARD_STATUS:
			return {
				...state,
				loading: true
			};
		case actions.GET_DASHBOARD_STATUS_SUCCESS_RESULT:
			return {
				...state,
				loading: false,
				DashboardStatus: action.result
			};
		case actions.GET_DASHBOARD_STATUS_ERROR_RESULT:
			return {
				...state,
				loading: false
			};




		// Get block transaction by txn#
		case actions.GET_BLOCK_TRANSACTION:
			return {
				...state,
				loading: true
			};

		case actions.GET_BLOCK_TRANSACTION_SUCCESS_RESULT:
			console.log(action.result)
			return {
				...state,
				loading: false,
				Blocktransaction: action.result
			};
		case actions.GET_BLOCK_TRANSACTION_ERROR_RESULT:
			return {
				...state,
				loading: false,
				Blocktransaction: {}
			};


		// Get transaction table data
		case actions.GET_TRANSACTIONS_DATA:
			return {
				loading: false,
				TransactionTableData: {}
			};

		case actions.GET_TRANSACTIONS_DATA_SUCCESS_RESULT:
			return {
				...state,
				loading: false,
				TransactionTableData: action.result
			};
		case actions.GET_TRANSACTIONS_DATA_ERROR_RESULT:
			return {
				...state,
				loading: false
			};

		// Get blocks
		case actions.GET_BLOCKS:
			return {
				...state,
				loading: true
			};
		case actions.GET_BLOCKS_SUCCESS_RESULT:
			// console.log(action.result)
			return {
				...state,
				loading: false,
				Blocks: action.result
			};
		case actions.GET_BLOCKS_ERROR_RESULT:
		console.log(action);
			return {
				...state,
				loading: false,
				Blocks:action
			};

		// Get transactions/transaction history
		case actions.GET_TRANSACTION:
			return {
				...state,
				loading: true
			};
		case actions.GET_TRANSACTION_SUCCESS_RESULT:
			// console.log(action.result)
			return {
				...state,
				loading: false,
				Transactions: action.result
			};
		case actions.GET_TRANSACTION_ERROR_RESULT:
			return {
				...state,
				loading: false,
				Transactions: action
			};

		// Get chain transaction history
		case actions.GET_CHAIN_TRANSACTION:
			return {
				...state,
				loading: true
			};
		case actions.GET_CHAIN_TRANSACTION_SUCCESS_RESULT:
			// console.log(action.result)
			return {
				...state,
				loading: false,
				chainTransactions: action.result
			};
		case actions.GET_CHAIN_TRANSACTION_ERROR_RESULT:
		console.log(action);
			return {
				...state,
				loading: false,
				chainTransactions: action
			};


		// Get transactions grpah
		case actions.GET_TRANSACTION_GRAPH:
			return {
				...state,
				loading: true
			};
		case actions.GET_TRANSACTION_GRAPH_SUCCESS_RESULT:
			// console.log(action.result)
			return {
				...state,
				loading: false,
				TransactionGraph: action.result
			};
		case actions.GET_TRANSACTION_GRAPH_ERROR_RESULT:
			return {
				...state,
				loading: false,
				TransactionGraph: action,
			};

		default:
			return state;
	}
}