export function parsePayload(payload) {
	debugger;
	let parsedResponse = {};

	// const { data, status } = payload;
	if (payload && payload.code === 200) {
		parsedResponse.statusCode = 0;
		parsedResponse.successMessage = payload.info;
		parsedResponse.response = payload ? payload : [];
	} else {
		parsedResponse.statusCode = 1;
		parsedResponse.errMessage = payload.info;
		parsedResponse.response = null;
	}

	return parsedResponse;
}

export function parseGraphPayload(payload) {
	debugger;
	let parsedResponse = {};

	// const { data, status } = payload;
	if (payload && payload.status === 200) {
		parsedResponse.statusCode = 0;
		parsedResponse.successMessage = payload.message;
		parsedResponse.response = payload ? payload : [];
	} else {
		parsedResponse.statusCode = 1;
		parsedResponse.errMessage = payload.info;
		parsedResponse.response = null;
	}

	return parsedResponse;
}