import actions from "./actions";

const initState = {
    GraphNode: {},
    GraphLink: {}
};
export default function reducer(state = initState, action) {
    switch (action.type) {

        // Get Graph Node
        case actions.GET_GRAPH_NODE:
            return {
                ...state,
                loading: true
            };
        case actions.GET_GRAPH_NODE_SUCCESS_RESULT:
            // console.log(action.result)
            return {
                ...state,
                loading: false,
                GraphNode: action.result
            };
        case actions.GET_GRAPH_NODE_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                GraphNode:{}
            };




        // Get Graph Link
        case actions.GET_GRAPH_LINKS:
            return {
                ...state,
                loading: true
            };

        case actions.GET_GRAPH_LINKS_SUCCESS_RESULT:
            console.log(action.result)
            return {
                ...state,
                loading: false,
                GraphLink: action.result
            };
        case actions.GET_GRAPH_LINKS_ERROR_RESULT:
            return {
                ...state,
                loading: false,
                GraphLink: {}
            };

        default:
            return state;
    }
}