import { all } from 'redux-saga/effects';
import BlockExplorerSagas from './blockExplorer/sagas';
import ModelExplorerSagas from './modelExplorer/sagas';
import ReceiptsSagas from './receipts/sagas';
import QuerySagas from './query/sagas';
import ApiCallSagas from './apicalls/sagas';
import LogSagas from './logs/sagas';
import HealthcheckSagas from './healthcheck/sagas';

export default function* rootSaga(getState) {
   yield all([
        BlockExplorerSagas(),
        ModelExplorerSagas(),
        ReceiptsSagas(),
        QuerySagas(),
        ApiCallSagas(),
        LogSagas(),
        HealthcheckSagas(),
   ]);
}

