const actions = {

    // Get Graph Node
    GET_GRAPH_NODE: "GET_GRAPH_NODE",
    GET_GRAPH_NODE_SUCCESS_RESULT: "GET_GRAPH_NODE_SUCCESS_RESULT",
    GET_GRAPH_NODE_ERROR_RESULT: "GET_GRAPH_NODE_ERROR_RESULT",

    getGraphNode: (data) => ({
        type: actions.GET_GRAPH_NODE,
        payload: { data }
    }),
    getGraphNodeSuccess: (saveStatusCode, result) => ({
        type: actions.GET_GRAPH_NODE_SUCCESS_RESULT,
        saveStatusCode,
        result

    }),
    getGraphNodeError: (saveStatusCode, message) => ({
        type: actions.GET_GRAPH_NODE_ERROR_RESULT,
        saveStatusCode,
        message
    }),

     // Get Graph Links
     GET_GRAPH_LINKS: "GET_GRAPH_LINKS",
     GET_GRAPH_LINKS_SUCCESS_RESULT: "GET_GRAPH_LINKS_SUCCESS_RESULT",
     GET_GRAPH_LINKS_ERROR_RESULT: "GET_GRAPH_LINKS_ERROR_RESULT",
 
     getGraphLink: (data) => ({
         type: actions.GET_GRAPH_LINKS,
         payload: { data }
     }),
     getGraphLinkSuccess: (saveStatusCode, result) => ({
         type: actions.GET_GRAPH_LINKS_SUCCESS_RESULT,
         saveStatusCode,
         result
 
     }),
     getGraphLinkError: (saveStatusCode, message) => ({
         type: actions.GET_GRAPH_LINKS_ERROR_RESULT,
         saveStatusCode,
         message
     }),

}


export default actions; 
