const actions = {

    // Get Training Topics
    GET_QUERY_DATA: "GET_QUERY_DATA",
    GET_QUERY_DATA_SUCCESS_RESULT: "GET_QUERY_DATA_SUCCESS_RESULT",
    GET_QUERY_DATA_ERROR_RESULT: "GET_QUERY_DATA_ERROR_RESULT",

    getQueryData: (data) => ({
        type: actions.GET_QUERY_DATA,
        payload: { data }
    }),
    getQueryDataSuccess: (saveStatusCode, result) => ({
        type: actions.GET_QUERY_DATA_SUCCESS_RESULT,
        saveStatusCode,
        result

    }),
    getQueryDataError: (saveStatusCode, message) => ({
        type: actions.GET_QUERY_DATA_ERROR_RESULT,
        saveStatusCode,
        message
    }),
}
export default actions;