import { all, takeEvery, put, call } from 'redux-saga/effects';
import  actions from './actions';
import * as dataAccess from 'utils/ajax';
// import * as ReduxConstants from 'constants/redux-constants'
import * as NetworkConstants from 'constants/api-constants';


// const APP_CONSTANTS = require('../../constants/app-constants');
const getRequest = async (url, payload) =>
	await dataAccess.get(url, payload);

// const postRequest = async (url, payload) =>
// 	await dataAccess.post(url, payload);

function* getParamLogsData(data) {
    debugger
    const  { payload }  = data;
    let url ;
    url = NetworkConstants.PARAM_LOGS;
    try {
        // console.log("Get blocks URL: ", url, " \n\n: payload", payload);
        let response = yield call(getRequest, url, payload)
        console.log('Get param logs Response', response);
        if (200 === response.code) {
            yield put(actions.getParamLogsDataSuccess(response.code, response.res));
        } else {
            debugger;
            yield put(actions.getParamLogsDataError(response.code, response.message));
        }
    } catch (error) {
        // alert('Failed to fetch Blocks API');
        yield put(actions.getParamLogsDataError(1));
    }
}

function* getGraphLogsData(data) {
    debugger
    const  { payload }  = data;
    let url ;
    url = NetworkConstants.DGRAPH_LOGS;
    try {
        // console.log("Get blocks URL: ", url, " \n\n: payload", payload);
        let response = yield call(getRequest, url, payload)
        console.log('Get dgraph logs Response', response);
        if (200 === response.status) {
            yield put(actions.getGraphLogsDataSuccess(response.status, response.content));
        } else {
            debugger;
            yield put(actions.getGraphLogsDataError(response.status, response.message));
        }
    } catch (error) {
        // alert('Failed to fetch Blocks API');
        yield put(actions.getGraphLogsDataError(1));
    }
}

export default function* rootSaga() {
    yield all(
        [
            takeEvery(actions.GET_PARAM_LOGS, getParamLogsData),
            takeEvery(actions.GET_DGRAPH_LOGS, getGraphLogsData),
        ]);
}